import {Component, Input, OnDestroy, OnInit} from '@angular/core';
import {FormBuilder, FormGroup, Validators, ReactiveFormsModule } from "@angular/forms";
import {Subscription} from "rxjs";
import { Emotions } from '../model/emotions-enum';
import { Channel } from '../model/channel-enum';
import {NgbActiveModal, NgbModal, NgbModule} from '@ng-bootstrap/ng-bootstrap';


@Component({
  selector: 'app-emotions-form',
  templateUrl: './emotions-form.component.html',
  styleUrls: ['./emotions-form.component.scss']
})
export class EmotionsFormComponent implements OnInit {

  @Input()
  emotion: string;
  form: FormGroup;
  private form_subscriber: Subscription;
  isValid: boolean = false;
  channelList: string[] = [Channel.ANDROID_APP, Channel.BROWSER_EXTN, Channel.IOS_APP, Channel.MS_TEAMS, Channel.WEBSITE];
  initialDropdownValue = '';
  tooltipMessage = '';

  constructor(
    public activeModal: NgbActiveModal,
    private formBuilder: FormBuilder
  ) {}

  private createForm() {
    this.form = this.formBuilder.group({
      emotions:[this.emotion],
      username: [''],
      cause: [''],
      channel: [''],

    });

    this.form_subscriber = this.form.valueChanges.subscribe(data => {
      this.isValid = this.form.valid;
      if(this.isValid){
        this.tooltipMessage = '';
      }
    });
  }

   submitForm() {
  
    console.log('emotions form values: ' + this.form.getRawValue());
    this.activeModal.close(this.form.getRawValue());
  }

  onCancelled() {
    this.activeModal.close(false);
  }

  ngOnInit(): void {
    this.createForm();
  }


}




