import { Injectable } from '@angular/core';
import { NgbModal, NgbModalOptions } from '@ng-bootstrap/ng-bootstrap';
import { EmotionsFormComponent } from '../components/emotions-form/emotions-form.component';
import { Emotions } from '../components/model/emotions-enum';

@Injectable({
  providedIn: 'root'
})
export class DialogService {
modalOption: NgbModalOptions = {};
  constructor(private modalService: NgbModal) {
  this.modalOption.backdrop = 'static';
  this.modalOption.keyboard = false; 
  }

  public saveForm(
    title: string,
    emotion: string
  ) : Promise<any>
{
  console.log('came here')
  const modalRef = this.modalService.open(EmotionsFormComponent, this.modalOption);
  modalRef.componentInstance.title = title;
  if(emotion != null || undefined){
    modalRef.componentInstance.emotion = emotion;
  }
  return modalRef.result;
}
}
